'use strict';
import {testimonialData} from "/data/testimonials.js";

const e = React.createElement;

const Testimonials = () => {
  
  const RenderSliders = () => (
    testimonialData.map((item) => (
        <div key={item.id} className="testimonials-slide">
          <div className="testimonials-slide__slide bottom-curves bottom-curves--white-black">
          <div className="testimonials-slide__rating">
            <ul>
            {
                  Array.from(Array(item.stars).keys()).map((index) => {
                    return <li key={index}> <figure> <img src={require('/assets/icons/star.svg')} alt="star" /></figure> </li>
                  })
            }
            </ul>
          </div>
          <span className="font-xsmall">{item.name}</span>
          <p className="paragraph">
            {item.review}
          </p>
        </div>
      </div>
    ))
  )


    return (
      <div className="testimonials-slider">
        <RenderSliders />
      </div>
    )
}

const domContainer = document.querySelector('#testimonialsComponent');
ReactDOM.render(e(Testimonials), domContainer);	